import { computed } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: 'guanjianciheader',
  props: {
    checknum: Number
  },
  setup(props) {
    const router = useRouter();
    const toup = () => {
      router.go(-1);
    };
    const showchecknum = computed(() => {
      if (props.checknum === 0) {
        return false;
      } else {
        return true;
      }
    });
    return {
      toup,
      showchecknum
    };
  }
};